function SearchBox() {
  const [show, setShow] = useState(false);
  const [risk, setRisk] = useState(0);
  const [eta, setETA] = useState("");

  const [data, setData] = useState({});

  const riskMapper = {
    Low: 1,
    Moderate: 2,
  };

  const etaCal = () => {
    estimate.forEach((val) => {
      if (
        val.HS_CODE === parseInt(data.hs) &&
        val.Destination.toLowerCase().trim() === data.to.toLowerCase().trim() &&
        val.Origin.toLowerCase().trim() === data.from.toLowerCase().trim()
      ) {
        setETA(val.ETD);
        return;
      }
    });
  };

  const riskCal = () => {
    riskData.forEach((val) => {
      if (
        val.Destination.toLowerCase().trim() === data.to.toLowerCase().trim() &&
        val.Origin.toLowerCase().trim() === data.from.toLowerCase().trim()
      ) {
        setRisk(riskMapper[val.Risk_category]);
        return;
      }
    });
  };
  const calculate = () => {
    if (data !== null && data.from && data.to && data.hs) {
      etaCal();
      riskCal();
      setShow(true);
    } else {
      setRisk(-1);
    }
  };

  return (
    <Card>
      <SoftBox p={2}>
        <SoftTypography variant="button" color="text" fontWeight="regular">
          Predictor
        </SoftTypography>
        <SoftBox mt={3}>
          <Grid container spacing={3}>
            <Grid item xs={12} lg={3}>
              <SoftInput
                placeholder="From"
                value={data.from}
                onChange={(e) => {
                  data.from = e.target.value;
                  setData({ ...data });
                }}
              />
            </Grid>
            <Grid item xs={12} lg={3}>
              <SoftInput
                placeholder="To"
                value={data.to}
                onChange={(e) => {
                  data.to = e.target.value;
                  setData({ ...data });
                }}
              />
            </Grid>
            <Grid item xs={12} lg={2}>
              <SoftInput
                placeholder="HS_CODE"
                value={data.hs}
                onChange={(e) => {
                  data.hs = e.target.value;
                  setData({ ...data });
                }}
              />
            </Grid>
            <Grid item xs={12} lg={2}>
              <SoftButton
                variant="gradient"
                color="info"
                onClick={() => {
                  calculate();
                }}
              >
                Search
              </SoftButton>
            </Grid>
          </Grid>
          {show && (
            <Grid container spacing={2} mt={3}>
              <Grid item xs={8} lg={4}>
                <SoftBox mb={1}>
                  <SoftTypography variant="h6" color="text" fontWeight="medium" textAlign="center">
                    ETA{" "}
                    <SoftTypography variant="h6" color="text" fontWeight="regular">
                      {eta + " days"}
                    </SoftTypography>
                  </SoftTypography>
                </SoftBox>
              </Grid>
              <Grid item xs={8} lg={4}>
                <SoftBox mb={1}>
                  <SoftTypography variant="h6" color="text" fontWeight="medium" textAlign="center">
                    Risk{" "}
                    {risk === 1 ? (
                      <SoftTypography variant="h6" color="success" fontWeight="regular">
                        Low
                      </SoftTypography>
                    ) : risk === 2 ? (
                      <SoftTypography variant="h6" color="warning" fontWeight="regular">
                        Moderate
                      </SoftTypography>
                    ) : (
                      <SoftTypography variant="h6" color="error" fontWeight="regular">
                        High
                      </SoftTypography>
                    )}
                  </SoftTypography>
                </SoftBox>
              </Grid>
            </Grid>
          )}
        </SoftBox>
      </SoftBox>
    </Card>
  );
}

export default SearchBox;
